(function() {
  /*-------ABOUT-------*/
  //Elements
  var sectionAbout = document.getElementById('about'),
    titleAbout = sectionAbout.querySelector('.about__title'),
    observedTxtElements = sectionAbout.querySelectorAll('.fade-in'),
    firstImgAbout = sectionAbout.querySelector('.slide-dwn'),
    secondImgAbout = sectionAbout.querySelector('.shrink-dwn'),
    wordWork = sectionAbout.querySelector('.syllable-word');

  animateAboutSection = function() {
    //animate text content
    //Desktop
    var txtContentOptionsDesktop = {
      root: null,
      rootMargin: '0px 0px -10% 0px',
      threshold: 0.9
    };
    //callback function
    var onIntersectionDesktop = function(entries, observerTxtContent) {
      entries.forEach(function(entry) {
        //hidde elements
        entry.target.style.opacity = '0';

        if (entry.intersectionRatio > 0.5) {
          slideInUpward(entry.target);
          observerTxtContent.unobserve(entry.target);
        }
      });
    };

    //Mobile devices
    var txtContentOptionsMobile = {
      root: null,
      rootMargin: '-5px',
      threshold: [0.08, 0.3, 0.6]
    };
    //callback function
    var onIntersectionMobile = function(entries, observerTxtContent) {
      entries.forEach(function(entry) {
        entry.target.style.opacity = '0';
        if (entry.intersectionRatio > 0) {
          if (entry.intersectionRatio >= 0.55) {
            entry.target.style.opacity = 1;
          } else {
            entry.target.style.opacity = entry.intersectionRatio;
          }
        }
      });
    };

    //Create observer
    var observerTxtContent;

    var createObserverTxtContent = function() {
      var w = parseInt(window.innerWidth),
        //h = parseInt(window.innerWidth),
        vport = 1259, // "large" viewport size 1260px - desktop layout
        onIntersection,
        txtContentOptions;

      if (w <= vport) {
        onIntersection = onIntersectionMobile;
        txtContentOptions = txtContentOptionsMobile;
      } else {
        onIntersection = onIntersectionDesktop;
        txtContentOptions = txtContentOptionsDesktop;
      }

      observerTxtContent = new IntersectionObserver(
        onIntersection,
        txtContentOptions
      );
    };

    function watchElements(elements) {
      if (elements) {
        createObserverTxtContent();
        for (let i = 0; i < elements.length; i++) {
          observerTxtContent.observe(elements[i]);
        }
      } else return;
    }
    watchElements(observedTxtElements);

    //animate imgs
    //animating first img
    var slideInDwnImg = function(node) {
      var child1 = node.getElementsByTagName('div'),
        child2 = node.getElementsByTagName('img');

      gsap.set(child1, {
        //slide in background
        backgroundColor: 'rgb(100, 100, 100)'
      });
      gsap.to(child1, {
        duration: 0.8,
        top: 0,
        ease: 'sine.out'
      });

      gsap.to(child2, {
        delay: 0.7,
        duration: 0.1,
        opacity: 1
      });

      gsap.to(child1, {
        delay: 0.7,
        duration: 1,
        top: '100%',
        ease: 'sine.out'
      });

      gsap.from(child2, {
        delay: 1.2,
        duration: 1,
        scaleY: 1.05
      });
      gsap.to(node, {
        delay: 0.5,
        duration: 1,
        boxShadow:
          '10px 1px 30px rgba(28, 32, 28, 0.3), -10px 1px 30px rgba(28, 32, 28, 0.3)',
        ease: 'expo.out'
      });
    };

    //animating second img
    var shrinkDwnImg = function(node) {
      var node2 = wordWork;
      gsap.to(node, {
        delay: 0.2,
        duration: 1,
        scaleY: 1
      });
      gsap.to(node2, {
        delay: 0.8,
        duration: 1,
        left: '6%',
        ease: 'sine.out'
      });

      gsap.fromTo(
        node2,
        {
          opacity: 0
        },
        //change this to opcity:1
        {
          duration: 2,
          opacity: 1,
          ease: 'expo.out'
        }
      );
    };

    var onIntersectionFirstImg = function(entries, observerImgReveal) {
      entries.forEach(function(entry) {
        var img = entry.target.querySelector('img');
        img.style.opacity = 0;
        if (entry.intersectionRatio >= 0.5) {
          slideInDwnImg(entry.target);
          observerImgReveal.unobserve(entry.target);
        }
      });
    };

    var onIntersectionSecondImg = function(entries, observerImgReveal) {
      entries.forEach(function(entry) {
        if (entry.intersectionRatio > 0) {
          shrinkDwnImg(entry.target);
          observerImgReveal.unobserve(entry.target);
        } else return;
      });
    };

    //OPTIONS
    //options_first img
    var optionFirstImg = {
      root: null,
      threshold: 0.7
    };
    //options_second img
    var optionSecondImg = {
      root: null,
      //rootMargin: "0px 0px 0px 0px",
      threshold: 0.6
    };

    //OBSERVERS
    //create observer for first img
    var observerFirstImg = new IntersectionObserver(
      onIntersectionFirstImg,
      optionFirstImg
    );
    //create observer for second img
    var observerSecondImg = new IntersectionObserver(
      onIntersectionSecondImg,
      optionSecondImg
    );
    observerFirstImg.observe(firstImgAbout);
    observerSecondImg.observe(secondImgAbout);
  };
  /*---END--ABOUT-------*/
})();
