/*--------SKILLS-------*/
(function() {
  var skillSet = document.querySelector("#skillSet"),
    skills = skillSet.querySelectorAll(".skills__skill"),
    icons = skillSet.querySelectorAll(".skills__icon"),
    skillName = skillSet.querySelectorAll(".skills__skill-name"),
    txtContent = document.querySelector(".skills__text-content");

  showUpElementsSkillsSection = function() {
    var optionsIcon = {
        root: null,
        rootMargin: "0px 0px 5% 0px",
        threshold: 0.78
      },
      optionsTxt = {
        root: null,
        rootMargin: "0px 0px -10% 0px",
        threshold: 0.6
      };
    //text content
    var onIntersectionTxt = function(entries, observer) {
      entries.forEach(function(entry) {
        var title = entry.target.previousElementSibling.querySelector("h2"),
          content = entry.target.querySelector("p");
        //hidde elements before animation begins
        content.classList.add("hidden");
        gsap.set(title, {
          y: "101%"
        });

        if (entry.intersectionRatio > 0.5) {
          gsap.to(title, {
            y: 0,
            ease: "power3.out",
            duration: 1
          });
          slideInUpward(content);
          observerTxtContent.unobserve(entry.target);
        }
      });
    };

    var onIntersectionIcon = function(entries, observer) {
      entries.forEach(function(entry) {
        var skillArray = entry.target.querySelectorAll(".skills__skill"),
          skillIconArray = entry.target.querySelectorAll(".icon"),
          skillNameArray = entry.target.querySelectorAll("p");

        if (skillArray && skillArray.length) {
          // hidde elements before animation begins

          for (let i = 0; i < skillIconArray.length; i++) {
            gsap.set(skillIconArray[i], {
              x: "200%"
            });
            skillIconArray[i].classList.add("hidden");
          }

          for (let i = 0; i < skillNameArray.length; i++) {
            skillNameArray[i].classList.add("hidden");
            //skillNameArray[i].style.opacity = 0;
          }

          //animate icons
          var triggerIconAnimation = function() {
            for (let i = 0; i < skillArray.length; i++) {
              skillArray[i].classList.add("animate");
            }
          };

          //animate skillname
          var fadeInSkillNames = function() {
            gsap.fromTo(
              skillNameArray,
              {
                opacity: 0,
                x: "80px"
              },
              {
                delay: 2.05,
                opacity: 1,
                x: 0,
                stagger: 0.08,
                duration: 1.2,
                ease: "power3.out"
              }
            );
          };

          if (entry.intersectionRatio > 0.5) {
            for (let i = 0; i < skillArray.length; i++) {
              gsap.to(skillArray[i].querySelector(".icon"), {
                duration: 0.5,
                ease: Expo.easeInOut,
                onComplete: triggerIconAnimation,
                fadeInSkillNames
              });
            }
            observer.unobserve(entry.target);
          } else if (entry.intersectionRatio <= 0) {
            for (let i = 0; i < skillIconArray.length; i++) {
              skillIconArray[i].classList.remove("animate");
              skillIconArray[i].classList.add("hidden");
              skillNameArray[i].classList.add("hidden");
            }
          }
        } else return;
      });
    };

    var observerIcon = new IntersectionObserver(
      onIntersectionIcon,
      optionsIcon
    );
    var observerTxtContent = new IntersectionObserver(
      onIntersectionTxt,
      optionsTxt
    );
    observerIcon.observe(skillSet);
    observerTxtContent.observe(txtContent);
  };
})();
